import React from "react"
import GeneralLayout from "../layouts/general-layout"
import { Helmet } from "react-helmet"
import "../style/index.scss"
import { graphql } from 'gatsby'

export default function Home({data}) {
  return <GeneralLayout>
    <Helmet>
      <title>Savvas Stephanides</title>
    </Helmet>

    <div className="homepage padding content-width">
      <h2>Showcase</h2>
      <ul className="spacer">
        {
          data.allShowcase.nodes.map((showcase) => 
            <li><a href={`/showcase/${showcase.slug}`}>{showcase.name.replace(".yml", "")}</a></li>
          )
        }
      </ul>

      <h2 className="spacer">Articles</h2>
      <ul className="spacer">
        {
          data.allBlogPost.nodes.map((post) => 
            <li><a href={`/post/${post.slug}`}>{post.name}</a></li>
          )
        }
      </ul>
    </div>
  </GeneralLayout>
}

export const query = graphql`
query {
  allBlogPost {
    nodes {
      slug
      name
    }
  }
  allShowcase {
    nodes {
      slug
      name
    }
  }
}
`